    padding-left: 5px;
  }
}

.navbar-toggle {
  border: none;
  background-color: transparent;
  font-size: 2rem;
  color: white;
  cursor: pointer;
}

.navbar-menu {
  display: none;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar-menu.active {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navbar-item {
  margin: 0.5rem 0;
}

.navbar-link {
  font-size: 1.2rem;
  font-weight: normal;
  color: white;
  text-decoration: none;

}

.navbar-link :hover {
  box-shadow: 0px 2px 2px #8B4513;
}

     /* Container styling */
                 .about-container {
                   max-width: 800px;
                   /* Limit the width of the container */
                   margin: 0 auto;
                   /* Center the container */
                   padding: 20px;
                   /* Add padding */
                   font-family: 'Arial', sans-serif;
                   /* Font family */
                   line-height: 1.6;
                   /* Line height for readability */
                   color: #333;
                   /* Text color */
                 }
        
                 .about-heading {
                   font-size: 2.5rem;
                   /* Main heading size */
                   margin-bottom: 20px;
                   /* Space below heading */
                   color: #7c360a;
                   /* Coffee brown */
                   text-align: center;
                   /* Center the heading */
                 }
        
                 .about-subheading {
                   font-size: 1.8rem;
                   /* Subheading size */
                   margin: 20px 0 10px;
                   /* Space around subheading */
                   color: #7c360a;
                   /* Coffee brown */
                 }
        
                 .about-text {
                   font-size: 1rem;
                   /* Text size */
                   margin-bottom: 15px;
                   /* Space below text */
                 }
        
                 .about-list {
                   margin: 0;
                   /* Removes default margin */
                   padding: 0;
                   /* Removes default padding */
                   list-style-type: none;
                   /* Remove default bullet points */
                   display: flex;
                   /* Use flexbox for the list */
                   flex-direction: column;
                   /* Stack items vertically */
                   align-items: flex-start;
                   /* Align items to the start */
                 }
        
                 .about-list li {
                   display: flex;
                   /* Use flexbox for alignment */
                   align-items: center;
                   /* Center items vertically */
                   margin: 5px 0;
                   /* Adds vertical spacing between each item */
                   padding-left: 30px;
                   /* Space for custom bullet */
                   position: relative;
                   /* Position for custom bullet */
                   width: 100%;
                   /* Full width for responsiveness */
                 }
        
                 .about-list li::before {
                   content: '';
                   /* Create a custom bullet */
                   position: absolute;
                   /* Position it absolutely */
                   left: 0;
                   /* Align it to the left */
                   top: 50%;
                   /* Center it vertically */
                   transform: translateY(-50%);
                   /* Adjust for perfect centering */
                   width: 8px;
                   /* Width of the bullet */
                   height: 8px;
                   /* Height of the bullet */
                   background-color: black;
                   /* Color of the bullet */
                   border-radius: 50%;
                   /* Make it circular */
                 }
        
             
                                 